import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';

import jobData from './data/data.json'
import { CardType } from "./types/componentsType"

import ResizeHook from './hook/ResizeHook'
import ThemeHook from './hook/ThemeHook'

import MainLayout from './Layouts/MainLayout'
import DetailLayout from './Layouts/DetailLayout'


function App() {

  const Device = ResizeHook()
  const Theme = ThemeHook()

  return (
    <Router>
      <Routes>
        <Route path='/dev-jobs/' element={<MainLayout Theme={Theme} Device={Device} />}  />
        <Route path='/dev-jobs/id::id' element={<DetailLayout Theme={Theme} Device={Device} />} />
      </Routes>
    </Router>

  );
}





export default App;


